<template>
	<section class="merchant-enter" id="merchant-enter">
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm">
			<!-- <el-form-item label=""  label-width="200px" prop="address">
				<v-distpicker :province="select.province" :city="select.city" :area="select.area" @province="onProvince" @city="onCity" @selected="onSelected"></v-distpicker>
			</el-form-item> -->
			<el-form-item label="" label-width="200px" prop="address" class="search-hezi">
				<el-input v-model="ruleForm.address" id="ser" placeholder="" class="wid280"></el-input>
				<el-button type="primary" class="mapbtn"  @click="searchbtn"></el-button>
			</el-form-item>

			<el-form-item label="" label-width="200px">
				<p>{{ ruleForm.longitude+","+ruleForm.latitude}}</p>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<map-view :longitude="ruleForm.longitude" :latitude="ruleForm.latitude" @listenToChildEvent="showsite" @listenTolongitude="getResult" ref="MapView"></map-view>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
			</el-form-item>	
			 
		</el-form>


	   
	</section>
	
</template>

<script>

import VDistpicker from 'v-distpicker';/**/
import MapView from '@/components/Map1';/**/
import { merchantSave,industryListall,gssUpload,storeCheckname} from '@/api/shopApi';
// 
/*Vue.filter('onlyNumber', function (value) {
  return value.replace(/[^/d]/g,''); 
});*/
export default {
	components: {
		VDistpicker,
		MapView	
	},

    mounted:function(){


    },
	methods: {
		/**/
		showsite(lng,lat){
			this.ruleForm.longitude=lng;
			this.ruleForm.latitude=lat;
		},
		/*map*/
		searchbtn(){
			var addr=this.ruleForm.address;
			this.$refs.MapView.searchMap(addr);
		},
		//input
		getResult:function(h){
			this.ruleForm.address=h;
		}
	},
	data() {
		return {
			

			identPic1:config.ident1,
			identPic2:config.ident2,
			//
			curCateName:[],
			stCateName:'',
			categoryData: [],
			industryCateId:[],
			ident1:'',
			ident2:"",
			
            secoundCategoryData:[],
            dialogVisible4:false,
          	dialogVisible3:false,

			dialogVisible1:false,
			dialogVisible2:false,
			height:300,
			selCity:"",
			selArea:'',
			selProvince:'',
			sel:'',
			isAdd:1,
			select: { 
				province: '',
			    city: '',
			    area: ''
			},
			isShop:1,
			shopHtml:'',
			categoryBarTitle1: '',
			categoryBarTitle2: '',
			categoryBarTitle3: '',
			categoryBarTitle4: '',
			categoryBarTitle5: '',
			uploadTishi1:"800*800pxjpgjpegpng,5MB",
			uploadTishi2:"",
			exampleSrc:'',
			dialogVisible:false,
			src1:"http://2.hapn.cc:20080/n/00802v_b009W1MMTZ0a02w8.jpg",

			searchsite:"",
			selcity:"",
			listLoading:false,
			searchKeyArr:[],

			ruleForm: {
				industry:'',
				industryCateIdList:[],
				identityPic1:'',
				identityPic2:'',
				enterpriseLicence:'',
				searcBarCode:'',
				enterpriseName:'',
				orgCode: '',
				legalPerson: '',
				identityNumber: '',
				name: '',
				selIndustry:[],
				detailedAddress: '',
				contactName:'',
			    contactMobile:'',
			    identityPic:'',
			    longitude:'',
		latitude:'',
			    address:''
				
			},
	        rules: {
	          	
	        }

		}
	}
}
</script>
<style lang="scss">
.merchant-enter{
	.address + .el-form-item__error{
		display:none;
	}
	/* .hangye{
		.el-input__inner{
			display: none;
		}
	} */
	.requireHezi{
		position:relative;
		.require{
			position:absolute;
			color:#ff4949;
		}
	}
}

</style>



