<template>
	<section class="merchant-enter" id="merchant-enter">
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm">
			<el-button type="text" class="selector-btn" @click="selector('ruleForm')">{{shopHtml}}</el-button>
			<div v-if="isShop==1">				
				<category-bar :title="categoryBarTitle1"></category-bar>
				<el-form-item label="" prop="enterpriseName"  label-width="200px">
					<el-input :maxlength="30" v-model="ruleForm.enterpriseName" placeholder=""  class="wid400"  ></el-input>
				</el-form-item>
				<el-form-item label="/" label-width="200px" class='requireHezi'>
					<span class="require" style='left:-180px;'>*</span>
					<upload-pictures :note="uploadTishi1" :url="ruleForm.enterpriseLicence" :listen="'listenToPic1'" :picSize='"5.MB"' @listenToPic1="sucpic1"  ref="uploadPic1"></upload-pictures>
				</el-form-item>
				<el-form-item label="" prop="enterpriseLicence"  label-width="200px" class='updata'>
					<el-input v-model="ruleForm.enterpriseLicence" class="wid280"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="200px" prop="orgCode">
					<el-input :maxlength="20" v-model="ruleForm.orgCode" placeholder="" class="wid400"></el-input>
				</el-form-item>
			</div>

			<div v-if="isShop==1">	
				<category-bar :title="categoryBarTitle2"></category-bar>
			</div>
			<div v-else>	
				<category-bar :title="categoryBarTitle5"></category-bar>
			</div>
			<div v-if="isShop==1">
				<el-form-item label="" label-width="200px" prop="legalPerson">
					<el-input  :maxlength="30" v-model="ruleForm.legalPerson" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item label="" label-width="200px" prop="identityNumber">
					<el-input :maxlength="18" v-model="ruleForm.identityNumber" placeholder="" class="wid400"></el-input>
				</el-form-item>
			</div>
			<div v-else>
				<el-form-item label="" label-width="200px" prop="legalPerson">
					<el-input :maxlength="30" v-model="ruleForm.legalPerson" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item label="" prop="legalMobile" label-width="200px">
					<el-input :maxlength="11" v-model="ruleForm.legalMobile" placeholder="" class="wid400"></el-input>
				</el-form-item>
				
				<el-form-item label="" label-width="200px" prop="identityNumber">
					<el-input :maxlength="18" v-model="ruleForm.identityNumber"  placeholder="" class="wid400"></el-input>
				</el-form-item>
			</div>
			<el-form-item :label="ident1" label-width="200px"  class="requireHezi">
				<div v-if="isShop==1">
					<span class="require" style='left:-120px;'>*</span>
				</div>
				<div v-else>
					<span class="require" style='left:-90px;'>*</span>
				</div>
				<upload-pictures :note="uploadTishi2" :url="ruleForm.identityPic1" :listen="'listenToPic2'" :picSize='"800.KB"' @listenToPic2="sucpic2" ref="uploadPic2"></upload-pictures>
				<div class="example">
					<div @click="iconSimple(2)"><img :src='identPic2'/></div>
					<span></span>
				</div>
				
			</el-form-item>
			<el-form-item label="" prop="identityPic1"  label-width="200px" class='updata'>
				<el-input v-model="ruleForm.identityPic1" class="wid280"></el-input>
			</el-form-item>
			<el-form-item :label="ident2" label-width="200px" class="requireHezi">
				<div v-if="isShop==1">
					<span class="require" style='left:-120px;'>*</span>
				</div>
				<div v-else>
					<span class="require" style='left:-90px;'>*</span>
				</div>
				<upload-pictures :note="uploadTishi2" :url="ruleForm.identityPic2" :listen="'listenToPic3'" :picSize='"800.KB"' @listenToPic3="sucpic3"  ref="uploadPic3"></upload-pictures>

				<div class="example">
					<div @click="iconSimple(1)"><img :src='identPic1' /></div>
					<span></span>
				</div>
				
			</el-form-item>
			<el-form-item label="" prop="identityPic2"  label-width="200px" class='updata'>
				<el-input v-model="ruleForm.identityPic2" class="wid280"></el-input>
			</el-form-item>

			<div v-if="isShop==1">
				<category-bar :title="categoryBarTitle3"></category-bar>
				<el-form-item label="" prop="contactName" label-width="200px">
					<el-input :maxlength="30" v-model="ruleForm.contactName" placeholder="" class="wid400"></el-input>
				</el-form-item>
				<el-form-item  label="" prop="contactMobile" label-width="200px">
					<el-input  :maxlength="11" v-model="ruleForm.contactMobile" name="username" placeholder="" class="wid400"></el-input>
				</el-form-item>	
			</div>

			<category-bar :title="categoryBarTitle4"></category-bar>
			<el-form-item  label="" prop="name"  label-width="200px">
				<el-input :maxlength="20" v-model="ruleForm.name" placeholder=""  class="wid400" @blur="findName"></el-input>
			</el-form-item>
			<el-form-item label=" "  label-width="200px" class="requireHezi">
				<span class="require" style='left:-133px;'>*</span>
				<el-button class='selIndustryBtn' @click="selCategory"></el-button>
				<div class="myIndustry" v-if="ruleForm.selIndustry.length">
					<h4></h4>
					<ul>
						<li v-for="(item,index) in ruleForm.selIndustry">
							<b>{{item[0]}}>{{item[1]}}</b>
							<el-button type="text" @click="delIndustry(index)"></el-button>
						</li>
					</ul>
				</div>
			</el-form-item>
			<!-- <el-form-item label="" label-width="200px"  prop="selIndustry">
				<el-input  :maxlength="11" v-model="ruleForm.selIndustry" name="username" placeholder="" class="wid400"></el-input>
			</el-form-item> -->
			
			<el-form-item label=""  label-width="200px" prop="address">
				<v-distpicker :province="select.province" :city="select.city" :area="select.area" @province="onProvince" @city="onCity" @selected="onSelected"></v-distpicker>
			</el-form-item>
			<el-form-item label="" label-width="200px" prop="address" class="search-hezi">
				<el-input v-model="ruleForm.address" id="ser" placeholder="" class="wid280"></el-input>
				<el-button type="primary" class="mapbtn" @click="searchbtn"></el-button>
				<ol class="search-ol" v-if="searchKeyArr.length">
					<li>123</li>
				</ol>
			</el-form-item>

			<el-form-item label="" label-width="200px">
				<p>{{ ruleForm.longitude+","+ruleForm.latitude}}</p>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<map-view :longitude="ruleForm.longitude" :latitude="ruleForm.latitude" @listenToChildEvent="showsite" @listenTolongitude="getResult" ref="MapView">
				</map-view>
			</el-form-item>
			<el-form-item label="" label-width="200px">
				<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
			</el-form-item>	
			 
		</el-form>

		<el-dialog title="" :visible.sync="dialogVisible3" custom-class="big-img">
		    <img :src="identPic1"/>
		</el-dialog>

		<el-dialog title="" :visible.sync="dialogVisible4" custom-class="big-img">
		    <img :src="identPic2"/>
		</el-dialog>

		<el-dialog
		    title=""
		    top="30%"
		    :show-close='false'
		    :close-on-click-modal='false'
			:visible.sync="dialogVisible1"
			custom-class="suc"
			size="tiny">
			<div class="suc-string">
				<span class="el-icon-circle-check"></span>
				<div>
					<h2></h2>
			    	<p>1-5</p>
			    	<p></p>
				</div>
			</div>
		    
		    <span slot="footer" class="dialog-footer">
			    <el-button type="primary" @click="getBack"></el-button>
		    </span>
		</el-dialog>

		<el-dialog
	        title=""
	        top="30%"
	        :visible.sync="dialogVisible2"
	        custom-class="relevance-good"
	        size="tiny">
	      <div class="">
	        <el-row>
		      <el-col :span="8">
		        <category-menu title="" v-if="categoryData.length" :categoryData="categoryData" @categoryClick="firstHandle" :arrKey="'secondIndustryList'">
		        </category-menu>
		      </el-col>
		      <el-col :span="8">
		        <category-menu title="" v-if="secoundCategoryData.length" :categoryData="secoundCategoryData" @categoryClick="secondHandle" :arrKey="'catePath'"></category-menu>
		      </el-col>
		    </el-row>
		    <div class="category-nav-breadcrumb">
		      <span class="icon-arrow-top arrow-top-a"><span class="icon-arrow-top arrow-top-b"></span></span>
		      <el-breadcrumb separator=">">
		        <span class="breadcrumb-tips"></span>
		        <template v-if="curCateName.length" v-for="(item,index) in curCateName">
		        	<b v-if="index!=0" class="el-breadcrumb__separator">/</b>
		        	<span v-for="(value,index) in item">
		        		<b v-if="index==1" class="el-breadcrumb__separator">&gt;</b>
						<b>{{value}}</b>
		        	</span>
		        </template>
		      </el-breadcrumb>
		    </div>
	      </div>
	        
	        <span slot="footer" class="dialog-footer">
	          <el-button type="primary"  @click="industrySel"></el-button>
	          <el-button @click="dialogVisible2 = false"></el-button>
	        </span>
	    </el-dialog>
	   
	</section>
	
</template>

<script>
import CategoryBar from '@/components/CategoryBar.vue'/**/
import CategoryMenu from '@/components/CategoryMenu2.vue'/**/
import UploadPictures from '@/components/UploadPictures.vue'/**/
import VDistpicker from 'v-distpicker';/**/
import MapView from '@/components/Map1';/**/
import { merchantSave,industryListall,gssUpload,storeCheckname} from '@/api/shopApi';
export default {
	components: {
		CategoryBar,
		CategoryMenu,
		UploadPictures,
		VDistpicker,
		MapView	
	},
	data() {
		var validatePhone = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	        	var first=value.slice(0,1);
		        if ((value.length!=11)||(first!=1)) {
		          	callback(new Error(''));
		        }else{
		        	callback();
		        }
	        }
	    };
	    var validateNumber1= (rule, value, callback) => {
          var reg =/^[0-9a-zA_Z]+$/g;
          if (!value.match(reg)) {
            callback(new Error(''));
          } else {
            callback();
          }
	    };
	    var validateNumber2= (rule, value, callback) => {
	        var reg =/(^\d{0,18}$)|(^\d{17}(\d|X|x)$)/g;
            if (!value.match(reg)) {
	            callback(new Error(''));
	        } else {
	            callback();
	        }
	    };
	    var validateNumber3= (rule, value, callback) => {
	        var reg =/^\d+$/g;
            if (!value.match(reg)) {
	            callback(new Error(''));
	        } else {
	            callback();
	        }
	    };
		return {
			

			identPic1:config.ident1,
			identPic2:config.ident2,
			//
			curCateName:[],
			stCateName:'',
			categoryData: [],
			industryCateId:[],
			ident1:'',
			ident2:"",
			
            secoundCategoryData:[],
            dialogVisible4:false,
          	dialogVisible3:false,

			dialogVisible1:false,
			dialogVisible2:false,
			height:300,
			selCity:"",
			selArea:'',
			selProvince:'',
			sel:'',
			isAdd:1,
			select: { 
				province: '',
			    city: '',
			    area: ''
			},
			isShop:1,
			shopHtml:'',
			categoryBarTitle1: '',
			categoryBarTitle2: '',
			categoryBarTitle3: '',
			categoryBarTitle4: '',
			categoryBarTitle5: '',
			uploadTishi1:"800*800pxjpgjpegpng,5MB",
			uploadTishi2:"",
			exampleSrc:'',
			dialogVisible:false,
			src1:"http://2.hapn.cc:20080/n/00802v_b009W1MMTZ0a02w8.jpg",

			searchsite:"",
			selcity:"",
			listLoading:false,
			searchKeyArr:[],

			ruleForm: {
				industry:'',
				industryCateIdList:[],
				identityPic1:'',
				identityPic2:'',
				enterpriseLicence:'',
				searcBarCode:'',
				enterpriseName:'',
				orgCode: '',
				legalPerson: '',
				identityNumber: '',
				name: '',
				selIndustry:[],
				detailedAddress: '',
				contactName:'',
			    contactMobile:'',
			    identityPic:'',
			    longitude:'',
		latitude:'',
			    address:'',
			    legalMobile:'',
				
			},
	        rules: {
	        	enterpriseLicence: [
	            	{ required: true, message: '/', trigger: 'blur' }
	          	],
	          	identityPic1: [
	            	{ required: true, message: '', trigger: 'blur' }
	          	],
	          	identityPic2: [
	            	{ required: true, message: '', trigger: 'blur' }
	          	],
	          	enterpriseName: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
	          	],
	          	orgCode: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 1, max: 20, message: '', trigger: 'blur' },
	            	{ validator:validateNumber1,trigger: 'blur'},
	            	{ validator:validateNumber1,trigger: 'change'}
	          	],
	          	legalPerson: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 2, max: 30, message: '', trigger: 'blur' }
	          	],
		        identityNumber: [
		            { required: true, message: '', trigger: 'blur' },
	            	{ min: 18, max: 18, message: '', trigger: 'blur' },
	            	 { validator:validateNumber2,trigger: 'blur'},
	            	 { validator:validateNumber2,trigger: 'change'}
		        ],
	          	name: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
	          	],
	          	detailedAddress:[
	          		{ required: true, message: '', trigger: 'blur' },
	          	],
	          	contactName: [
	            	{ required: true, message: '', trigger: 'blur' },
	            	{ min: 2, max: 30, message: '', trigger: 'blur' }
	          	],
		        contactMobile: [
		        	{ required: true,validator: validatePhone,trigger: 'blur' },
		        	{ validator:validateNumber3,trigger: 'blur'},
		        	{ validator:validateNumber3,trigger: 'change'}

	          	],
		        legalMobile: [
		        	{ required: true,validator: validatePhone,trigger: 'blur' },
		        	{ validator:validateNumber3,trigger: 'blur'},
		        	{ validator:validateNumber3,trigger: 'change'}

	          	],
		        address:[
		           { required: true, message: '', trigger: 'blur' }
		        ],
		        industry:[
		          { required: true, message: '', trigger: 'blur' },
		        ]
	          	
	        }

		}
	},

    mounted:function(){
    	if(localStorage.getItem('merchant')==1){
    		this.$router.push({ path: '/store/message'});
    	}

	    //
	    industryListall({}).then((res) => {
	    	if(res.data.code==0){
	    		this.categoryData = res.data.data;
	    	}   
	    })    

    },
	methods: {
		/**/
		showsite(lng,lat){
			this.ruleForm.longitude=lng;
			this.ruleForm.latitude=lat;
		},
		/*map*/
		searchbtn(){
			var addr=this.ruleForm.address;
			this.$refs.MapView.searchMap(addr);
		},
		//input
		getResult:function(h){
			this.ruleForm.address=h;
		},
		/**/
	    findName(val){
	        if(!this.ruleForm.name){
	          return false;
	        }
	        storeCheckname({"storeName":this.ruleForm.name}).then((res) => {
	            if(res.data.code==1){
	              this.$message.error('');
	            }
	        });
	    },
		//
		sucpic1(url){
			this.ruleForm.enterpriseLicence=url;
		},
		//
		sucpic2(url){
			this.ruleForm.identityPic1=url;
		},
		//
		sucpic3(url){
			this.ruleForm.identityPic2=url;
		},

		//
		selCategory(){
			this.dialogVisible2=true;
			this.curCateName=[];
			this.secoundCategoryData=[];
			this.industryCateId=[];

		},

		//
		firstHandle (row, index){

			this.stCateName='';
			this.stCateName=row.industryCateName;
	        if(row.secondIndustryList.length) {
	            this.secoundCategoryData = row.secondIndustryList;
	        }else {
	            this.secoundCategoryData = [];
	        }
        },

        //
        secondHandle (row, index) {
        	var maxArr=this.ruleForm.selIndustry.concat(this.curCateName);
   			
        	if(maxArr.length>9){
        		this.$message({message: '10',type: 'warning'});
		        return false;
        	}
        	
        	var strJson=this.curCateName.join(",");
        	var str=this.stCateName+","+row.industryCateName;

        	var selIndustryJson =this.ruleForm.selIndustry.join(",");
        	if((strJson.indexOf(str)>=0)||(selIndustryJson.indexOf(str)>=0)){
        		this.$message({message: '',type: 'warning'});
        	}else{
        		this.curCateName.push([this.stCateName,row.industryCateName]);
        		this.industryCateId.push(row.industryCateId);
        	}
        	

        },
        //
        industrySel(){
        	if (this.curCateName.length==0) {
        		return false;
        	}else{
        		this.ruleForm.selIndustry=this.ruleForm.selIndustry.concat(this.curCateName);
        		this.ruleForm.industryCateIdList=this.ruleForm.industryCateIdList.concat(this.industryCateId);
        		this.dialogVisible2=false;

        	}
        },
        //
        delIndustry(index){
        	console.log(index);
			this.$delete(this.ruleForm.selIndustry, index);
			this.$delete(this.ruleForm.industryCateIdList, index);
        },
		/**/
		iconSimple(v){
			if(v==1){
				this.dialogVisible3=true;
			}else{
				this.dialogVisible4=true;
			}
			
		},

		/**/
		onProvince(data) {
		    this.select.province=data.value;
		    if(data.code){
		    	this.selProvince=data.value;
		    }else{
		    	this.selProvince='';
		    }
		    this.selCity='';
		    this.selArea="";
	    },
	    onCity(data){
	    	this.select.city=data.value;
	    	if(data.code){
		    	this.selCity=data.value;
		    }else{
		    	this.selCity='';
		    }
		    this.selArea="";
	    },
	    onSelected(data){
	    	this.select.area=data.area.value;
	    	if(data.area.code){
		    	this.selArea=data.area.value;
		    }else{
		    	this.selArea='';
		    }
	    },
	    /*map*/
		searchbtn(){
			var addr=this.ruleForm.address;
			this.$refs.MapView.searchMap(addr);
		},
		/**/
		showsite(lng,lat){alert(455)
			this.ruleForm.longitude=lng;
			this.ruleForm.latitude=lat;
		},
		/**/
		selector(formName){
			this.$refs[formName].resetFields();/**/
			
			this.$refs.uploadPic2.revise("");//
			this.$refs.uploadPic3.revise("");//
			this.ruleForm.selIndustry=[];
			this.ruleForm.industryCateIdList=[];
			//this.select= { province: '',city: '',area: ''};
			if(this.isShop==1){/*isShop1*/
				this.isShop=0;
				this.shopHtml="";
				this.ident1='';
				this.ident2="";
				this.$refs.uploadPic1.revise("");//
				
			}else{
				this.isShop=1;
				this.shopHtml="";
				this.ident1='';
				this.ident2="";
			}
			this.select= { 
				province: '',
			    city: '',
			    area: ''
			};
		},
		/**/
		submitForm(formName) {
		    this.$refs[formName].validate((valid) => {
	            if (valid) {
	            	if(!this.ruleForm.industryCateIdList.length){
	            		this.$message({
				          message: '',
				          type: 'warning'
				        });
				        return false;
	            	}

		      		if(!this.selProvince){//
						this.$message({
				          message: '',
				          type: 'warning'
				        });
				        return false;
					}else{//
						this.sel=this.selProvince;
						if(this.selCity){
							this.sel+="*"+this.selCity;
						}
						if(this.selArea){
							this.sel+="*"+this.selArea;
						}
					}

					var storeType=this.isShop==1?1:2;
		        	this.listLoading = true;


		            var para = new URLSearchParams();
		            para.append('userId',config.uid);
		            para.append('shopType',storeType);
		            para.append('enterpriseName',this.ruleForm.enterpriseName);
		            para.append('enterpriseLicence',this.ruleForm.enterpriseLicence);
		            para.append('orgCode',this.ruleForm.orgCode);
		            para.append('legalPerson',this.ruleForm.legalPerson);
		            para.append('identityNumber',this.ruleForm.identityNumber);
		            para.append('identityPic',this.ruleForm.identityPic1+','+this.ruleForm.identityPic2);		          
		            para.append('contactName',this.ruleForm.contactName);
		            para.append('contactMobile',this.ruleForm.contactMobile);
		            para.append('name',this.ruleForm.name);
		            para.append('industryCateIdList',this.ruleForm.industryCateIdList);
		            para.append('address',this.sel+'*'+this.ruleForm.address);
		            para.append('longitude',Number(this.ruleForm.longitude*1000000));
					para.append('latitude',Number(this.ruleForm.latitude*1000000));

					if(this.isShop!=1){//
						para.append('legalMobile',this.ruleForm.legalMobile);
						
					}

		            merchantSave(para).then((res)=> {
		            	this.listLoading = false;
		            	if(res.data.code==0){
		            		this.dialogVisible1=true;
		            	}else{
		            		this.$message.error(res.data.message);
		            	}
		                
		            }).catch((res)=> {
		                this.listLoading = false;
		                this.$message.error('');
		            });
		      	}else {
		        	return false;
		      	}
		    });
		},
		/**/
	    getBack(){
	    	this.dialogVisible1 = false;
	    	localStorage.setItem("merchant",1);
	    	window.location.reload();
	    	
	    }

	}
}
</script>
