<template>
	<div class="maps-hezi">
		<div id="maps" style="width:400px;height:300px;"></div>
		<input type="text" id="suggestId" v-model="inputHtml" @input="detailed"/>
		<div id="searchResultPanel" style="border:1px solid #c0c0c0;width:150px;height:auto;display:none">
			{{resultHtml}}
		</div>
		<!-- <el-form-item label="" label-width="200px" class="search-hezi">
			<el-input v-model="ruleForm.address" id="ser" placeholder="" class="wid280"></el-input>
		</el-form-item> -->
		<div id="r-result" v-if="searchResult.length">
			<ol>
				<li v-for="item in searchResult" @click="clickKeys(item)">{{item}}</li>
			</ol>
		</div>
		
	</div>

</template>
<script>
export default{
data(){
return{
			searchResult:[],
			inputHtml:'2',
			resultHtml:'',
			dis:"none"
}
},

props:{ //data
		longitude:{}, //
		latitude:{} //
},

	methods:{
		//
		creatmap(lng,lat){
			var p1=this.longitude;
			var p2=this.latitude;

			if(arguments.length>0){
				p1=lng;
				p2=lat;
			}

			if(!p1&&!p2){// 
				p1='116.404';
				p2='39.915';
			}
			
			var point = new BMap.Point(p1,p2);
	map.centerAndZoom(point, 16);
			that.jump(point);
			that.addZoom();
			that.clickMap();
		},
		//input
		detailed(){
			that.$emit('listenTolongitude',this.inputHtml);//
		},


		setPlace(){
			map.clearOverlays();    //
			function myFun(){
				var point = local.getResults().getPoi(0).point;    //
				map.centerAndZoom(point, 16);
				that.jump(point);
				
			}
			var local = new BMap.LocalSearch(map, { //
			  onSearchComplete: myFun
			});
			local.search(myValue);
			that.clickMap();
		},

		//
		clickMap(){
			//
			map.addEventListener("click",function(e){
				var point = new BMap.Point(e.point.lng,e.point.lat); // 
				map.centerAndZoom(point,16); 
				that.jump(point);
				
			});
		},

		//
		jump(point){
			map.clearOverlays(); //
var marker = new BMap.Marker(point);// 
map.addOverlay(marker);// 
			marker.setAnimation(BMAP_ANIMATION_BOUNCE); //

			that.$emit("listenToChildEvent",point.lng , point.lat);//
		},

		//
		searchMap(addre){
		    var myGeo = new BMap.Geocoder();

		    myGeo.getPoint(addre, function(point){
		        if (point) {
		            map.centerAndZoom(point, 16);
		            map.addOverlay(new BMap.Marker(point));
		            that.jump(point);
		        }else{
		        	that.$message.error(',');

		        }
		    });
		},

		//
		addZoom(){
			map.enableScrollWheelZoom();//
   			map.enableKeyboard();//

   			//
			var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
			map.addControl(ctrl_nav);
			//
			var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
			map.addControl(ctrl_sca);
		},

		againAddr(addr){
			this.inputHtml=addr;
		}

	},
	mounted:function() {
		window.myValue='';
		window.that=this;
		window.map = new BMap.Map("maps");

		this.creatmap();

		window.ac = new BMap.Autocomplete({"input" : "suggestId",   //
			"location" : map
		});

		ac.addEventListener("onhighlight", function(e) {  //
			var str = "";
			var _value = e.fromitem.value;
			var value = "";
			if (e.fromitem.index > -1) {
				value = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
			}    
			str = "FromItem<br />index = " + e.fromitem.index + "<br />value = " + value;
			
			value = "";
			if (e.toitem.index > -1) {
				_value = e.toitem.value;
				value = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
			}    
			str += "<br />ToItem<br />index = " + e.toitem.index + "<br />value = " + value;
			//that.G("searchResultPanel").innerHTML =str;
			that.resultHtml=value;
		});

		
		ac.addEventListener("onconfirm", function(e) {    //
			var _value = e.item.value;
			myValue = _value.province +  _value.city +  _value.district +  _value.street +  _value.business;
			//that.G("searchResultPanel").innerHTML ="onconfirm<br />index = " + e.item.index + "<br />myValue = " + myValue;
			//that.resultHtml="onconfirm<br />index = " + e.item.index + "<br />myValue = " + myValue;
			that.resultHtml=myValue;
			//that.setPlace();
			that.$emit('listenTolongitude',myValue);//
		});


	}
}
</script>
<style lang="scss">
.maps-hezi{
	position:relative;
	#r-result{
		position:absolute;
		top:-85px;
		background: #fff;
	    border: 1px solid rgb(131, 162, 165);
	    z-index: 999;
	    li{
	    	padding:0px 10px;
	    	cursor:pointer;
	    }
	    li:hover{
	    	background:#eeeeee;
	    }
	}
	#suggestId{
		position:absolute;
		top: -121px;
	    width: 280px;
	    height: 36px;
	    padding-left: 10px;
	    border:1px solid #bfd8d9;
	}
	#suggestId:focus{
		outline:none;
		border-color: #41cac0;
	}
	#suggestId:hover{
		border-color: rgb(131, 162, 165);
	}
	#searchResultPanel{
		position:absolute !important;
		top:0;
	}

}
.load_hezi{
	.avatar-uploader{
		.el-upload{
			border-radius: 0 !important;
		}
	}
}
</style>



